import mongoose from 'mongoose';

const UserSchema = new mongoose.Schema({
    username: {
        type: String,
        required: true,
        unique: true,
        trim: true,
        minlength: 3,
    },
    email: {
        type: String,
        required: true,
        unique: true,
        trim: true,
        lowercase: true,
    },
    password: {
        type: String,
        required: true,
        minlength: 6,
    },
    xp: {
        type: Number,
        default: 0,
    },
    level: {
        type: Number,
        default: 1,
    },
    completedLessons: {
        type: [Number],
        default: [],
    },
    currentLesson: {
        type: Number,
        default: 1,
    },
    budgetChallenge: {
        isActive: {
            type: Boolean,
            default: false,
        },
        startDate: {
            type: Date,
        },
        lastCheckIn: {
            type: Date,
        },
        daysCompleted: {
            type: Number,
            default: 0,
        },
    },
    badges: [
        {
            type: String,
        },
    ],
    createdAt: {
        type: Date,
        default: Date.now,
    },
});

export default mongoose.models.User || mongoose.model('User', UserSchema);
